import React, { Component } from 'react';
import { connect } from 'react-redux'

import './Home.scss';

import background from '../../assets/background_black_code_trim.mov'

class Home extends Component {

    changeName = (event)=>this.props.changeName(event.target.value)

    render() {
        let name = this.props.name;
        return (
            <div className="Home">
                <video autoPlay muted loop id="background-video"><source src={background} type="video/mp4"/></video>
                <div>
                    <h1>{name}</h1>
                    <p>This is my personal website</p>
                    <input type="text" name="newName" onChange={this.changeName}></input>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state)=>{
  return {
    name: state.name
  }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        changeName: (name)=> {dispatch({type:'CHANGE_NAME', name:name})}
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);